.container {
    height: 100%;
    min-height: 100vh;
    width: 100%;
    background: linear-gradient(to right, #272727, #bdb4b4);
    display: flex;
    justify-content: center;
    align-items: center;
    overflow-y: hidden;
    overflow-x: hidden;
    position: fixed;
    z-index: var(--z-fixed);
}

.innerBox{
    min-width: 480px;
    height: fit-content;
    width: fit-content;
    background-color: #fff;
    box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.2);
    padding: 30px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    gap: 30px;
}

.footer{
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.footer .error{
    font-weight: bold;
    font-size: 0.875rem;
    color: #ff3300;
    text-align: center;
}

.footer button{
    outline: none;
    border: none;
    background-color: #242324;
    color: #fff;
    border-radius: 5px;
    font-weight: bold;
    font-size: 1rem;
    padding: 10px 16px;
    width: 100%;
    transition: 100ms;
    cursor: pointer;
}

.footer button:disabled{
    background-color: gray;
}

.footer button:hover{
    background-color: #575350;
}

.footer p{
    font-weight: 700;
    color: #000;
}

.footer p span a{
    font-weight: bold;
    color: #7a7171;
    letter-spacing: 1px;
    font-size: 1rem;
    text-decoration: none;
}
