.home__container {
    row-gap: 7rem;

}
.home__data{
    /* grid-template-columns: 166px repeat(2, 1fr); */
    padding-top: 0.5rem;
    padding-left: 20rem;
    /* column-gap: 2rem; */
    align-items: center;
}
 
.home__title{
    font-size: var(--big-font-size);
    margin-bottom: var(--mb-0-25);
}


.home__subtitle{
    position: relative;
    font-size: var(--h4-font-size);
    padding-left: 6.5rem;
    font-weight: var(--font-normal);
    margin-bottom: var(--mb-1);
}

.home__subtitle::before{
    content: '';
    position: absolute;
    width: 100px;
    height: 1px;
    background-color: var(--text-color);
    left: 0;
    top: 0.8rem;
}

.home__description{
    max-width: 400px;
    margin-bottom: var(--mb-3);
}



.home__scroll {
    margin-left: 9.25rem;
}

.wheel {
    animation: scroll 2s ease infinite;
}

@keyframes scroll {
    0% {
        transform: translateY(0);
    }

    30% {
        transform: translateY(3.75rem);
    }
}

.home__scroll-name {
    color: var(--title-color);
    font-weight: var(--font-medium);
    margin-right: var(--mb-0-25);
    margin-left: 2px;
}

.home__scroll-arrow {
    font-size: 1.25rem;
    color: var(--title-color);
}

/*=================BREAKPOINTS=======================*/
/* For large devices */
@media screen and (max-width: 992px) {
    .home__content{
        grid-template-columns: 100px repeat(2, 1fr);
        column-gap: 1.25rem;
    }

    .home__hand {
        width: 26px;
        height: 26px;
    }

    .home__subtitle{
        padding-left: 3.75rem;
        margin-bottom: var(--mb-1);
    }

    .home__subtitle::before {
        width: 42px;
        top: 0.8rem;
    }

    .home__description {
        max-width: initial;
        margin-bottom: var(--mb-2-5);
    }

     

    .home__scroll {
        margin-left: 7.5rem;
    }
  }
  
  /* For medium devices */
  @media screen and (max-width: 768px) {
    .home__content{
        grid-template-columns: 0.5fr 3fr;
        padding-top: 3.5rem;
    }

    

    .home__data {
        grid-column: 1/3;
    }

   
    .home__scroll {
        display: none;
    }
  }
  
